<template>
  <fixed-header>
  <div class="nav">
      <form @submit="addTodo">
          <input type="text" v-model="title" name="title" placeholder="Add Todo...">
          <input type="submit" value="Submit" class="btn">
      </form>
  </div>
  </fixed-header>
</template>

<script>
import uuid from 'uuid';
import FixedHeader from 'vue-fixed-header';
export default {
    name: "AddTodo",
    components: {
        FixedHeader
    },
    data(){
        return {
            title: ''
        }
    },
    methods: {
        addTodo(e){
            e.preventDefault();
            const newTodo = {
                id: uuid.v4(),
                title: this.title,
                completed: false
            }
            this.$emit('add-todo',newTodo);
            this.title = '';
        }
    }
}
</script>

<style scoped>
    *{
        margin: 3px;
    }
    form {
        display: flex;
    }
    input[type="text"] {
        flex: 10;
        padding: 5px;
    }
    input[type="submit"]{
        flex: 2;
    }
    .nav.vue-fixed-header--isFixed{
        position: sticky;
        left: 0px;
        top: 0px;
        width: 100%;
        
    }


</style>