<template>
  <div class="todo-item" v-bind:class="{'is-complete':todo.completed}">
      <p>
          <input type="checkbox" v-if="!todo.completed" name="" id="" v-on:change="markComplete">
          <input type="checkbox" v-if="todo.completed" checked name="" id="" v-on:change="markComplete">
          
            {{todo.title}}
          
          <button class="del" @click="$emit('del-todo', todo.id)">X</button>
          </p>
  </div>
</template>

<script>
export default {
    name: "TodoItem",
    props: ["todo"],
    methods: {
        markComplete(){
            this.todo.completed = !this.todo.completed;
        }
    }
}
</script>

<style scoped>
    .todo-item {
        background: #f4f4f4;
        padding: 10px;
        border: 2px #ccc dashed;
        border-bottom: none;
        margin-bottom: 0px;
        height: 50px;
        
    }
    .is-complete {
        text-decoration: line-through;
    }
    .del {
        background: #ff0000;
        color: #fff;
        border: none;
        padding: 5px 9px;
        border-radius: 50%;
        cursor: pointer;
        float: right;
        

    }
 
</style>